.todo {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;

  margin: 0 25px;
  padding: 0;

  gap: 20px;

  list-style: none;

  &__item {
    margin-bottom: 10px;

    font-size: 20px;
    font-weight: 400;
    font-family: sans-serif;

    border: 2px solid #543c52;
    border-radius: 10px;
    background-color: #edd2cb;
    color: #361d32;

    transition: all 1s;
    cursor: pointer;

    &:hover {
      transform: scale(1.02);
    }
  }
}
